@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&family=Spartan:wght@300;400;500&display=swap');


* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Spartan',sans-serif;
}

:root{
  --PrimaryColor: hsl(225,50%,48%);
  --SecondaryColor: hsl(180,17%,95%);
  --colorOne: hsl(15,74%,70%);
  --colorTwo: hsl(20,100%,82%);
  --HoverColor: hsl(225,50%,59%);
  --whiteColor: hsl(0,0%,100%);
  --blackColor: hsl(0,0%,12%);
  --textColor: hsl(240,1%,48%);
  --borderColor: hsl(0,0%,83%);
  --whiteColorDeam: hsl(0,0%,93%);
  --greyBg: hsl(0,0%,96%);
  --greyText: hsl(145,145,145);
  --inputColor: hsl(330,12%,97%);
  --boxShadow: box-shadow rgba(0,0,0,0.1)0px 10px 15px -3px, rgba(0,0,0,0.05) 0px 4px 6px -2px;
  --transition: .4s ease-in-out;
}



body {
  background: var(--whiteColor);
}

a{
  text-decoration: none;
}

li{
  list-style: none;
  cursor: pointer;
}

.section{
  padding: 4rem 0 2rem;
}

.container {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.icon {
  font-size: 1.3rem;
  cursor: pointer;
}

.flex {
  display: flex;
  align-items: center;
}

.grid {
  display: grid;
  align-items: center;
}

.none {
  display: none;
}

.btn {
  padding: .7rem 1.5rem;
  background: var(--PrimaryColor);
  border: none;
  outline: none;
  border-radius: 3rem;
  cursor: pointer;
  color: var(--whiteColor);

  a{
    color: var(--whiteColor);
    font-weight: 500;
  }

  &:hover {
    background: var(--HoverColor);
  }
}

img,video {
  width: 100%;
  height: auto;
}

input {
  font-size: 100%;
  color: var(--greyText);
}

p {
  font-size: 13px;
  color: var(--greyText);
  line-height: 22px;
}

h4{
  padding: 1rem 0;
  font-weight: 700;
  color: var(--blackColor);
}

h2 {
  font-size: 25px;
  padding: 1rem 0;
  color: var(--blackColor);
}



.navbar {
  position: fixed;
  flex-direction: column;
  width: 100%;
  z-index: 1000;

  .navbarOne,.navbarTwo{
    justify-content: space-between;
    padding: 1rem;
    width: 100%;
  }

  .navbarOne {
    background: var(--SecondaryColor);

    .icon:hover{
      color: var(--PrimaryColor);
    }

    .atb {
      gap: 1rem;

      span {
        color: var(--blackColor);
        font-size: 13px;
        cursor: pointer;

        &:hover {
          color: var(--PrimaryColor);
          font-weight: 400;
        }
      }

    }
  }

  .navbarTwo {
    display: flex;
    align-items: center;
    justify-content: space-between;
    top: 3rem;
    padding: 1.5rem 1rem;
    position: fixed;
    transition: .3s ease-in-out;

    .logoDiv{
      .logo{
        width: 20%;
      }
    }


    @media screen and (max-width: 768px){
      .navbarMenu{
        position: absolute;
        background: var(--SecondaryColor);
        padding: 1.5rem;
        border-radius: 10px;
        top: 5rem;
        right: -50%;
        width: 80%;
        transform: translateX(50%);
        z-index: 2000;
        box-shadow: rgba(0,0,0,0.1) 0 10px 15px -3px, rgba(0,0,0,0.05) 0 4px 6px -2px;
        border: 3px solid var(--whiteColor);
        transition: .4s ease-in-out;

        .menu{
          flex-direction: column;
          gap: .5rem;
          text-align: center;

          .listItem {
            padding: .5rem 0;
            cursor: pointer;
            color: var(--blackColor);

            &hover {
              color: var(--PrimaryColor);
              font-weight: 700;
            }
          }
        }

        .btnOne{
          display: block;
          justify-content: center;
          margin: 1rem auto auto;
        }
      }

      .btnTwo{
        display: none;
      }
    }

    .toggleIcon{
      .icon{
        font-size: 30px;
        color: var(--blackColor);
      }
    }

    .shownavBar{
      right: 50%;
      transition: .5s ease-in-out;
    }
  }

  .addBackgroundColor{
    background: var(--SecondaryColor);
    padding: .5rem 1rem;
    box-shadow: rgba(0,0,0,0.1) 0 10px 15px -3px, rgba(0,0,0,0.05) 0 4px 6px -2px;
  }
}



// Home stilizálása

.home{
  padding-top: 10rem;
  text-align: center;
  gap: 2rem;
  flex-direction: column;
  padding-bottom: 0;

  .mainText{
    color: var(--blackColor);
    font-weight: 700;
    font-size: 15px;
    line-height: 2.5rem;
  }

  .homeImages{
    width: 100%;
    margin: 1rem auto 0; // ez a széles képernyőkön változik majd
    justify-content: center;
    align-items: center;
    position: relative;
    border-radius: 5rem;

    .videoDiv{
      width: 100%;

      .video{
        border-radius: 10rem;
      }
    }

    .plane{
      position: absolute;
      width: 100%;
      top: -40%;
    }
  }
}



// Search stilizálása


.search{
  .sectionContainer{
    width: 100%;
    padding: 1.5rem;
    border: 2px solid var(--greyBg);
    border-radius: 16px;
    row-gap: 2rem;

    .btns{
      background: var(--SecondaryColor);
      padding: .5rem;
      border-radius: 1rem;
      flex-direction: column;
      width: 100%;
      margin: auto;

      .singleBtn{
        padding: .8rem 1.5rem;
        font-size: 13px;
        border-radius: 3rem;
        width: 80%;
        text-align: center;
        cursor: pointer;

        &:hover{
          background: var(--PrimaryColor);
          color: var(--whiteColor);
          font-weight: 500;
          transition: .4s ease;
        }
      }

      .active{
        background: var(--PrimaryColor);
        color: var(--whiteColor);
        font-weight: 500;
      }
    }

    .searchInputs{
      width: 100%;
      justify-content: center;
      flex-direction: column;
      gap: 1.5rem;

      .singleInput{
        .iconDiv{
          padding: 16px 10px;
          border-radius: 3rem;
          background: var(--SecondaryColor);
          margin-right: 1rem;

          .icon{
            color: var(--blackColor);
            opacity: .7;
          }
        }

        .texts{
          h4{
            padding-top: 0;
            padding-bottom: .7rem;
            color: var(--blackColor);
            font-weight: 700;
          }

          input{
            width: 95%;
            border: none;
            outline: none;
            background: transparent;

            &::placeholder{
              font-size: 12px;
              opacity: .5;
            }
          }
        }
      }

      .btnBlock{
        justify-content: center;
      }
    }
  }
}



// Support stilizálása


.support{
  .sectionContainer{
    .titlesDiv{
      text-align: center;
      small{
        letter-spacing: .8rem;
        text-transform: uppercase;
        font-size: 13px;
        color: var(--blackColor);
        line-height: 1.5rem;
      }
    }

    .infoDiv{
      margin-top: 2rem;
      gap: 1.5rem;

      .textDiv{
        gap: 1.5rem;

        .singleInfo{
          .number{
            background: var(--PrimaryColor);
            padding: 5px 12px;
            width: 40px;
            border-radius: 3rem;
            text-align: center;
            font-size: 13px;
            font-weight: 700;
            color: var(--whiteColor);
          }

          .colorOne{
            background: var(--colorOne);
          }
          .colorTwo{
            background: var(--colorTwo);
          }
        }
      }

      .imgDiv{
        margin: auto;
        img{
          width: 100%;
          max-width: 420px;
        }
      }
    }
  }
}



// Info stilizálása


.info{
  background: var(--SecondaryColor);
  .infoContainer{
    .titleDiv{
      flex-direction: column;
      text-align: center;
      gap: 1.5rem;
      justify-content: space-between;
    }

    .cardsDiv{
      gap: 1.5rem;
      margin-top: 3rem;


      .singleCard{
        background: var(--whiteColor);
        padding: 3rem 2rem;
        border-radius: 10rem;
        gap: 1rem;
        text-align: center;
        margin: auto;
        height: 320px;
        width: 90%;
        max-width: 250px;

        .iconDiv{
          justify-content: center;
          margin: auto;
          background: var(--PrimaryColor);
          width: max-content;
          height: 55px;
          padding: 1rem;
          border-radius: 3rem;

          .icon{
            color: var(--whiteColor);

          }
        }

        .cardTitle{
          color: var(--blackColor);
          font-weight: 700;
        }

        .colorOne{
          background: var(--colorOne);
        }
        .colorTwo{
          background: var(--colorTwo);
        }
      }
    }
  }
}



// Lounge stilizálása


.lounge{
  .sectionContainer{
    gap: 1.5rem;

    .imgDiv{
      margin: auto;
      img{
        width: 100%;
        max-width: 430px;
      }
    }

    .textDiv{
      .grids{
        margin-top: 1rem;
        gap: 1.5rem;

        .singleGrid{
          .gridTitle{
            display: block;
            padding-bottom: .5rem;
            font-weight: 500;
            color: var(--blackColor);
          }
          p{
            opacity: .6;
          }
        }
      }
    }
  }
}



// Travelers stilizálása


.travelers{
  .sectionContainer{
    text-align: center;

    .travelersContainer{
      margin-top: 1rem;
      gap: 1.5rem;

      .singleTraveler{
        position: relative;
        height: 320px;
        width: 85%;
        max-width: 250px;
        overflow: hidden;
        border-radius: 10rem;
        gap: 1rem;
        margin: auto;

        .destinationImage, .travelerImage, .travelerName{
          position: absolute;
        }

        .destinationImage{
          height: 100%;
          width: 100%;
          top: 0;
          right: 0;
          left: 0;
          bottom: 0;
          object-fit: cover;
        }

        .travelerImage{
          height: 50px;
          width: 50px;
          object-fit: cover;
          border-radius: 50%;
          bottom: 30px;
          left: 40%;
          transform: translateX(-50%);
          border: 5px solid rgba(225,225,225,0.637);
          transition: var(--transition);
          animation: animated 2.3s infinite;
        }
        
        @keyframes animated {
          0%{
            transform: translateY(0px);
          }
          50%{
            transform: translateY(8px);
          }
          100%{
            transform: translateY(0px);
          }
        }

        .travelerName{
          bottom: -100%;
          width: 100%;
          height: max-content;
          background: var(--whiteColor);
          padding: 1.5rem;
          transition: var(--transition);
          animation: animatedBorders 1.4s infinite;

          span{
            display: block;
            color: black;
            font-weight: 700;
          }

          p{
            color: var(--PrimaryColor);
            font-style: italic;
          }
        }


        @keyframes animatedBorders {
          0%{
            border-radius: 0;
          }
          50%{
            border-radius: 3rem;
          }
          100%{
            border-radius: 0;
          }
        }


        &:hover {
          .travelerName {
            bottom: 0;
            transition: .5s ease-in-out;
          }
          .travelerImage{
            bottom: 110px;
            transition: .5s ease-in-out;
          }
        }


      }
    }
  }
}



// Subscribe stilizálása


.subscribe{
  background: var(--SecondaryColor);

  .sectionContainer{
    text-align: center;
    .inputDiv{
      margin-top: 1rem;
      flex-direction: column;
      background: var(--whiteColor);
      border-radius: 1rem;

      input{
        border: none;
        outline: none;
        background: transparent;
        padding: 1rem .5rem;

        &::placeholder{
          font-size: 13px;
          opacity: .5;
        }
      }
      .btn{
        width: 100%;
      }
    }
  }
}



// Subscribe stilizálása


.footer{
  background: var(--SecondaryColor);

  .sectionContainer{
    gap: 2.5rem;
    padding-bottom: 5rem;

    .gridOne{
      gap: 1rem;
      justify-content: center;
      margin: auto;

      .logoDiv{
        .logo{
          width: 20%;
        }
      }

      .socialIcon{
        margin-top: 1rem;
        gap: 1rem;

        .icon{
          height: 50px;
          width: 30px;
          border-radius: 10rem;
          border: 1px solid var(--borderColor);
          box-shadow: var(--boxShadow);
          padding: .4rem;
          color: var(--greyText);

          &:hover{
            background: var(--PrimaryColor);
            transform: translateY(-7px);
            transition: var(--transition);
            border: 1px solid var(--PrimaryColor);
            color: var(--whiteColor);
          }
        }
      }
    }

    .footerLinks{
      display: grid;

      .linkTitle{
        color: var(--blackColor);
        font-weight: 700;
        display: block;
        padding-bottom: 1rem;
      }

      li a{
        display: block;
        color: var(--greyText);
        transition: var(--transition);
        padding: .3rem 0;
        opacity: .6;

        &:hover{
          transform: translateX(7px);
          color: var(--PrimaryColor);
          opacity: 1;
        }
      }
    }
  }
}




